import React from "react"
import Helmet from "react-helmet"
import Intro from "../components/Intro"
import PostListing from "../components/PostListing/PostListing"
import SEO from "../components/SEO/SEO"
import config from "../../data/SiteConfig"

class Index extends React.Component {
	mapPostList = () => {
		const postEdges = this.props.data.allMarkdownRemark.edges
		return postEdges.map(postEdge => ({
			path: postEdge.node.fields.slug,
			tags: postEdge.node.frontmatter.tags,
			cover: postEdge.node.frontmatter.cover,
			image: postEdge.node.frontmatter.image
				? postEdge.node.frontmatter.image.childImageSharp.original.src
				: `https://unsplash.it/300?image=${Math.floor(Math.random() * 1000 + 1)}`,
			title: postEdge.node.frontmatter.title,
			date: postEdge.node.frontmatter.date,
			excerpt: postEdge.node.excerpt,
			timeToRead: postEdge.node.timeToRead
		}))
	}
	render() {
		const postEdges = this.props.data.allMarkdownRemark.edges
		const postList = this.mapPostList()
		return (
			<div className="index-container">
				<Helmet title={config.siteTitle} />
				<SEO postEdges={postEdges} />
				<Intro />
				<PostListing postList={postList} />
			</div>
		)
	}
}

export default Index

export const pageQuery = graphql`
	query IndexQuery {
		allMarkdownRemark(limit: 6, sort: { fields: [frontmatter___date], order: DESC }) {
			edges {
				node {
					fields {
						slug
					}
					excerpt
					timeToRead
					frontmatter {
						title
						tags
						date
						image {
							childImageSharp {
								original {
									src
								}
							}
						}
					}
				}
			}
		}
	}
`



